<!-- 首诊-上传处方 -->
<template>
	<view class="column">
		<view class="line"></view>
		<view class="top-row">
			<image mode="aspectFill" style="width: 4rpx; height: 22rpx;" src="../../static/image/icon_zs.png"></image>
			<view class="txt-name">请上传{}在医疗机构的首诊处方单</view>
		</view>
		<view class="div">
			<image v-if="!this.isShow " @tap="goRegistration()" class="add-img" src="../../static/image/icon_add.png" mode="aspectFill"></image>
			<view v-if="this.isShow" >
				<image class="img-set" :src="this.imageUrl" mode="aspectFit"></image>
				<image class="img-dele" @tap="clean()" src="../../static/image/delete.png" mode="aspectFill"></image>
			</view>
		</view>
		<view class="update">提交</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isShow: false,
				imageUrl:''
			}
		},
		//状态栏中右上角事件监听
		onNavigationBarButtonTap(index) {

		},
		methods: {
			goRegistration() {
				//照相机
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					// sourceType: ['album'], //从相册选择
					success: (res) => {
						this.isShow=true;
						console.log("回调图片路径");
						this.imageUrl=res.tempFilePaths[0];
						console.log(JSON.stringify(res.tempFilePaths[0])); //要上传的图片路径
					}
				});
			},
			clean(){
				this.isShow=false;
				this.imageUrl="";
			}
		}
	}
</script>

<style>
	@import url("up-prescription.css");
</style>
